
header {
    color: white;
    padding: 1rem;
    text-align: center;
    position: relative;
    z-index: 1001;
}

.header-top {
    display: flex;
    align-items: center;
    padding: 1rem;
    color: white;
}

.header-top .logo img {
    height: 50px;
    width: auto;
}

.header-top .company-info {
    margin-left: 1rem;
}

.header-top .company-info h1 {
    margin: 0;
    font-size: 1.5rem;
}

.header-top .company-info p {
    margin: 0;
    font-size: 1rem;
}

nav {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0.5rem 2rem;
    flex-wrap: nowrap;
}

nav ul {
    display: flex;
    list-style-type: none;
    padding: 0;
    margin: 0;
    gap: 1rem;
    flex: 1 1 auto;
}

nav ul li {
    margin: 0;
}

nav ul li a {
    color: #ffffff;
    text-decoration: none;
    padding: 0.5rem 1rem;
    border-radius: 4px;
}

nav ul li a:hover {
    transform: scale(1.05);
}

.search-bar {
    display: flex;
    order: -1;
    background-color: #ffffff;
    border-radius: 4px;
    overflow: hidden;
}

.search-bar:hover {
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
}

.search-bar input {
    border: none;
    flex: 1;
    outline: none;
    font-size: 1rem;
}

.search-bar button {
    background-color: rgba(0, 0, 0, 0.0);
    border: none;
    color: #fff;
    cursor: pointer;
    transition: background-color 0.3s;
    display: flex;
}

.search-bar button:hover {
    background-color: #34495E;
    transform: scale(1.05);
}

@media (max-width: 768px) {
    nav ul {
        flex-direction: column;
    }

    nav ul li {
        margin: 0.5rem 0;
    }

    .search-bar {
        width: 100%;
        margin-top: 0rem;
    }

    .search-bar input {
        width: 80%;
    }

    .search-bar button {
        width: 20%;
    }
}

@media (max-width: 1024px) {
    nav ul {
        flex-direction: column;
    }

    nav ul li {
        margin: 0.5rem 0;
    }
}